import React, { useState } from "react"
import { Grommet, Box, Button, Text, Menu } from 'grommet';
import { grommet } from 'grommet/themes';
import '../pages/index.css'
import './i18n';
import { navigateTo } from 'gatsby-link';

const Home = ({ children, max = '560px' }) => {
  const [sidebar, setSidebar] = useState(false);
  return <Grommet full theme={grommet}>
      <Box
        gridArea="header"
        direction="row"
        align="center"
        pad={{ horizontal: 'medium', vertical: 'small' }}
        background="brand"
      >
        <Button margin={{ right: '20px'}} onClick={() => setSidebar(sidebar)}>
          <Text size="large">OpenFengShui</Text>
        </Button>
        <Menu label="Tools" items={[
          { label: 'QM Hour', onClick: () => { navigateTo('/qmdj/hour') } },
          { label: 'QM Day', onClick: () => { navigateTo('/qmdj/day') } },
          { label: 'QM Month', onClick: () => { navigateTo('/qmdj/month') } },
          { label: 'Flying Star', onClick: () => { navigateTo('/flyingstar/chart') } },
          { label: '8 Mansion', onClick: () => { navigateTo('/bazhai/chart') } },
          { label: 'Calendar', onClick: () => { navigateTo('/calendar/page') } },
        ]} />

        <Menu label="Videos" items={[
          { label: 'Metaphysics', onClick: () => { navigateTo('/videos/metaphysics') } },
        ]} />
      </Box>
      <Box 
        align="center"
        justify="center"
        pad="small"
      >
        <Box 
          
          fill="horizontal"
          width={{
          min: '360px',
          max
        }} >
          { children }
        </Box>
      </Box>
    </Grommet>
}

export default Home
